@import '../../globalVariable';

.forgetPassword {
  position: relative;
  margin: 20px 30px 0;
  .descTitle {
    margin: 23px 0 26px;
    text-align: center;
    font-size: $middleFontSize;
    font-family: PingFangSC-Regular;
  }
  .sendMessage {
    font-size: $middleFontSize;
    margin: 43px 0 20px;
    padding-left: 19px;
  }
  .canNotGetVercodeWrapper {
    margin-top: 20px;
    text-align: center;
  }
  .resetBtn {
    margin-top: 10px;
  }
  .checkboxWrapper {
    margin: 30px 0;
    text-align: center;
  }
  .passwordInputWrapper {
    margin-top: 29px;
    .passwordBlock {
      margin-bottom: 10px;
    }
  }
  .helpBtn {
    margin-top: 30px;
    @include linkButton;
  }
}